	module.exports = {"data":{"post":{"id":"/home/travis/build/h0ssimo/beta/content/posts/portfolio/2017-08-14--Leonard-Cohen/index.md absPath of file >>> MarkdownRemark","html":"<h2>Leonard Cohen Portrait - Tower Of Song</h2>\n<p>I’ve been deeply inspired by Leonard Cohen in many aspects of my life, so I decided to sculpt his earlier-times’ style as a tribute to him.</p>\n<h3>Inspiration</h3>\n<div class=\"gatsby-highlight\" data-language=\"text\">\n      <pre class=\"language-text\"><code class=\"language-text\">In his song &quot;Tower of Song,&quot; he mentions:</code></pre>\n      </div>\n<blockquote>\n<p>”<em>Now I bid you farewell, I don’t know when I’ll be back.<br />\nThey’re moving us tomorrow to that tower down the track.<br />\nBut you’ll be hearing from me baby, long after I’m gone.<br />\nI’ll be speaking to you sweetly from a window in the tower of song.</em>”</p>\n</blockquote>\n<p>So my work was really inspired by that.\n\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/leonard_cohen_bg_wide-b8b4ca694e6f47f15d07f78b7b33e185-222c6.jpeg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 56.22222222222223%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\" alt=\"leonard_cohen_bg_wide\" title=\"\" src=\"/static/leonard_cohen_bg_wide-b8b4ca694e6f47f15d07f78b7b33e185-78f2b.jpeg\" srcset=\"/static/leonard_cohen_bg_wide-b8b4ca694e6f47f15d07f78b7b33e185-dce19.jpeg 200w,\n/static/leonard_cohen_bg_wide-b8b4ca694e6f47f15d07f78b7b33e185-c1413.jpeg 400w,\n/static/leonard_cohen_bg_wide-b8b4ca694e6f47f15d07f78b7b33e185-78f2b.jpeg 800w,\n/static/leonard_cohen_bg_wide-b8b4ca694e6f47f15d07f78b7b33e185-222c6.jpeg 900w\" sizes=\"(max-width: 800px) 100vw, 800px\">\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<h3>Sculpting Process</h3>\n<p>I used Maya for creating the base meshes, ZBrush for adding details, and Transpose Master to pose him with.\nAll the accessories and clothing were done by hand.\nThe hair is Fiber Mesh and It was rendered using ZBrush and composed in Photoshop.</p>\n\n  <a class=\"gatsby-resp-image-link\" href=\"/static/reference_comarison-decb79f940243afc4d2ffc461d8aaae0-222c6.jpg\" style=\"display: block\" target=\"_blank\" rel=\"noopener\">\n  \n  <span class=\"gatsby-resp-image-wrapper\" style=\"position: relative; display: block; ; max-width: 800px; margin-left: auto; margin-right: auto;\">\n    <span class=\"gatsby-resp-image-background-image\" style=\"padding-bottom: 100%; position: relative; bottom: 0; left: 0; background-image: url(&apos;data:image/jpeg;base64,&apos;); background-size: cover; display: block;\">\n      <img class=\"gatsby-resp-image-image\" style=\"width: 100%; height: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px transparent;\" alt=\"reference_comarison\" title=\"\" src=\"/static/reference_comarison-decb79f940243afc4d2ffc461d8aaae0-78f2b.jpg\" srcset=\"/static/reference_comarison-decb79f940243afc4d2ffc461d8aaae0-dce19.jpg 200w,\n/static/reference_comarison-decb79f940243afc4d2ffc461d8aaae0-c1413.jpg 400w,\n/static/reference_comarison-decb79f940243afc4d2ffc461d8aaae0-78f2b.jpg 800w,\n/static/reference_comarison-decb79f940243afc4d2ffc461d8aaae0-222c6.jpg 900w\" sizes=\"(max-width: 800px) 100vw, 800px\">\n    </span>\n  </span>\n  \n  </a>\n    \n<p>I used a main reference for creating his portrait, but also used a lot of references to get an idea of how he should look.</p>\n<p>For higher resolution images, please visit my Artstation.</p>","fields":{"slug":"/Leonard-Cohen/","prefix":"portfolio/2017-08-14"},"frontmatter":{"title":"Leonard Cohen - Portrait","author":"Hossein Moayed","category":"portfolio","date":"2017-08-14T00:00:00.000Z","cover":{"childImageSharp":{"resize":{"src":"/static/leonard_cohen_bg-9d0fb21205914370de054d447bf0a122-ada8c.jpeg"}}}}},"authornote":{"id":"/home/travis/build/h0ssimo/beta/content/parts/author.md absPath of file >>> MarkdownRemark","html":"<p>To make it short, I’m a 3D Character Artist experienced in character pipelines for both animations and games.</p>"},"site":{"siteMetadata":{"facebook":{"appId":"298710090861252"}}}},"pathContext":{"slug":"/Leonard-Cohen/","prev":{"id":"/home/travis/build/h0ssimo/beta/content/posts/portfolio/2017-07-17--Mexican-Gangster/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/Mexican-Gangster/","prefix":"portfolio/2017-07-17"},"frontmatter":{"title":"The Mexican Gangster","category":"portfolio","tags":["real-time","game-art"],"date":"2017-07-17T00:00:00.000Z"}},"next":{"id":"/home/travis/build/h0ssimo/beta/content/posts/portfolio/2017-10-06--The-Orc/index.md absPath of file >>> MarkdownRemark","fields":{"slug":"/The-Orc/","prefix":"portfolio/2017-10-06"},"frontmatter":{"title":"The Brawler","category":"portfolio","tags":["fantasy","sculpture"],"date":"2017-10-06T00:00:00.000Z"}}}}

